.card {
    justify-content: center;
    align-items: center;
    text-align: center;
    top: 50px;
    width: 400px;
    margin: 10px auto;
    padding: 10px;
    border: 1px solid #000;
    border-radius: 5px;
    box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.5);
    user-select: none;
}

img {
    width: 25%;
    height: auto;
}

a {
    text-decoration: none;
    cursor: pointer;
    color: white;
}
